import * as C from "./common.js";

/**
 * tips 
 */
    
/**
 * 
 * 1. 
 * 2. options tips extendOptions, 
 * 3. options  extendOptions
 * @Author   Devin
 * @param    {[type]}                 options       
 * @param    {Object}                 extendOptions  
 * @return   {[type]}                               [description]
 */
function tips(options, extendOptions = {}) {
    if(!options)
        return this;

    this.C = C;

    // 
    if(C.isString(options) || C.isArray(options)) {
        let newOptions = {
            message: options
        };

        options = newOptions;

        if(C.isObj(extendOptions) && !C.isEmptyObj(extendOptions)) {
            options = C.extend({}, extendOptions, options);
        }
    }

    this.init(options);
}

tips.C = C;

